import React from 'react';
import '../styles/GalleryElement.css'


function GalleryElement(props) {

    return (
        <a href={props.projectUrl}
            onClick={e => {
                e.preventDefault();
                window.open(props.projectUrl);
            }}
            className="element"
        >
            <div className="inner__element">
                <span>{props.msg}</span>
                <img src={props.elementImage} alt="project preview" className="element__img"/>
            </div>
        </a>
    )
}


export default GalleryElement;